





































.index-footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 45px;
  display: flex;
  background-color: #e9ecef;
  a {
    color: #005cbf;
    display: flex;
    flex: 1; //flex
    flex-direction: column;
    align-items: center;
    justify-self: center;
    text-decoration: none;
  }
  a.router-link-active {
    color: red;
  }
}
