.index-footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 45px;
  display: flex;
  background-color: #e9ecef;
}
.index-footer a {
  color: #005cbf;
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  justify-self: center;
  text-decoration: none;
}
.index-footer a.router-link-active {
  color: red;
}
